export class Camera_Tools {
    /*
      UI
    */
    public static GetCamera(scene: BABYLON.Scene, name: string, id: string): BABYLON.Nullable<BABYLON.Camera> {
        for (var i = 0; i < scene.cameras.length; i++) {
            if (name == scene.cameras[i].name) {
                return scene.cameras[i];
            }
        }
        console.error(id + "----" + name);
        return null;
    }
}