import { VE_ActionBehaviour, VE_Actions } from "../../action";
import { UGUI_Tools } from "../trigger";
import { ParaParserUtility, VeryParas, ErrorInfo } from "../../utility";
import { ParaType, Severity } from "../../enum";
import { VeryNumber, VeryString, IVeryVar, VeryBool, VarTools, VeryVector3, VeryVector2 } from "../../variables";
import { GUIManager, VE_Error, VE_ErrorManager } from "../../../veryengine/global";


export class Action_GUI_Control extends VE_ActionBehaviour {

    public get ID(): string {
        return "GUI_Control|GUI_Combination|GUI|GUI";
    }

    public get className(): string {
        return "Action_GUI_Control";
    }


    private _controlStr: VeryString = new VeryString();

    //PanelIDUI
    private _panelModule: { [key: string]: BABYLON.GUI.Control[] } = {};
    //         112 =>  <11,112>
    private _currentLevelID: { [key: string]: string } = {};
    //00   ----
    private _zeroNotesID: { [key: string]: string } = {};
    //ID
    private _currentTotalID: string = "";
    //ID
    private _length: number = -1;
    //,
    private _max: { [key: string]: number } = {};
    private _min: { [key: string]: number } = {};
    // 
    private _navigationInf: string[] = [];
    private _navigationPoint: number = 0;
    private _switch: string = "";



    public active(): void {
        this._switch = this._controlStr.getValue().toString();
        let loopNumber: number;
        let isSpecial: boolean;
        let para = {
            state_input: this._switch,
            number: 0,
            is_special: true
        }
        if (!this.DealInputRequirements(para)) {
            console.error(this.ID + " " + this._switch + "");
            return;
        }
        this._switch = para.state_input;
        loopNumber = para.number;
        isSpecial = para.is_special;
        let tagString = this._switch.substring(0, this._length - 1);
        let lastNumber = this._switch[this._length - 1];
        if (lastNumber === '=') {
            if (this._currentLevelID[tagString]) {
                this.SetActive(this._panelModule[this._currentTotalID], false);
                this._currentTotalID = this._currentLevelID[tagString];
                this.SetActive(this._panelModule[this._currentTotalID], true);
            }
        }

        else if (lastNumber == '<') {
            if (this._currentLevelID[tagString] && this._currentLevelID[tagString][this._length - 1] === '0') {
                this.SetActive(this._panelModule[this._currentTotalID], false);
                this._currentLevelID[tagString] = this._zeroNotesID[tagString];
                this._currentTotalID = this._zeroNotesID[tagString];
                this.RecordNavigationInf(this._currentTotalID);
                this.SetActive(this._panelModule[this._currentTotalID], true);
            }
        }

        else if (lastNumber === '+') {
            this.SetActive(this._panelModule[this._currentTotalID], false);
            this._currentTotalID = this.PlusLoop(this._currentTotalID, loopNumber);
            this.RecordNavigationInf(this._currentTotalID);
            this._currentLevelID[tagString] = this._currentTotalID;
            this.SetActive(this._panelModule[this._currentTotalID], true);

        }
        else if (lastNumber === '-') {
            this.SetActive(this._panelModule[this._currentTotalID], false);
            this._currentTotalID = this.MinusLoop(this._currentTotalID, loopNumber);
            this.RecordNavigationInf(this._currentTotalID);
            this._currentLevelID[tagString] = this._currentTotalID;
            this.SetActive(this._panelModule[this._currentTotalID], true);
        }
        else if (lastNumber === ')') {
            if (this._navigationPoint < this._navigationInf.length - 1) {
                this.SetActive(this._panelModule[this._currentTotalID], false);
                let oldID = this._currentTotalID;
                this._navigationPoint++;
                this._currentTotalID = this._navigationInf[this._navigationPoint];
                this._currentLevelID[this._currentTotalID.substring(0, this._length - 1)] = this._currentTotalID;
                if (this._currentTotalID[this._length - 1] === '0' && oldID.startsWith(this._currentTotalID.substring(0, this._length - 1)))
                    this._zeroNotesID[this._currentTotalID.substring(0, this._length - 1)] = this._currentTotalID;
                this.SetActive(this._panelModule[this._currentTotalID], true);
            }
        }
        else if (lastNumber === '(') {
            if (this._navigationPoint !== 0) {
                this.SetActive(this._panelModule[this._currentTotalID], false);
                let oldID = this._currentTotalID;
                this._navigationPoint--;
                this._currentTotalID = this._navigationInf[this._navigationPoint];
                this._currentLevelID[this._currentTotalID.substring(0, this._length - 1)] = this._currentTotalID;
                if (this._currentTotalID[this._length - 1] === '0' && oldID.startsWith(this._currentTotalID.substring(0, this._length - 1)))
                    this._zeroNotesID[this._currentTotalID.substring(0, this._length - 1)] = this._currentTotalID;
                this.SetActive(this._panelModule[this._currentTotalID], true);
            }
        } else {
            if (this.PanelContainsKey(this._switch) && this._switch !== this._currentTotalID) {
                if (isSpecial) {
                    this._currentLevelID[tagString] = this._switch;
                } else {
                    this.SetActive(this._panelModule[this._currentTotalID], false);
                    this._currentLevelID[tagString] = this._switch;
                    this.RecordNavigationInf(this._currentTotalID);
                    if (lastNumber === '0' && this._currentTotalID.startsWith(tagString))
                        this._zeroNotesID[tagString] = this._currentTotalID;
                    this._currentTotalID = this._switch;
                    this.SetActive(this._panelModule[this._currentTotalID], true);
                }
            }
        }
        this._controlStr.setValue(this._currentTotalID);
    }

    private DealInputRequirements(data: any): boolean {
        data.number = 1;
        data.is_special = false;
        if (data.state_input === ")" || data.state_input === "(") {
            for (let i = 0; i < this._length - 1; i++) {
                data.state_input = "0" + data.state_input;
            }
            return true;
        } else if (data.state_input.length > this._length) {
            if (data.state_input.startsWith("@")) {
                data.is_special = true;
                data.state_input = data.state_input.substring(1);
            }
            else if (data.state_input[this._length - 1] === '+' || data.state_input[this._length - 1] === '-') {
                let numberStr = data.state_input.substring(this._length);
                var n = parseInt(numberStr);
                if (isNaN(n)) {
                    return false;
                }
                data.number = n;
                data.state_input = data.state_input.substring(0, this._length);
                return true;
            }
            else
                return false;
        }
        if (data.state_input.length === this._length)
            return true;
        else
            return false;
    }

    private RecordNavigationInf(current_id: string): void {
        if (this._navigationPoint < this._navigationInf.length - 1) {
            for (let i = this._navigationPoint + 1; i < this._navigationInf.length; i++) {
                this._navigationInf.splice(this._navigationPoint + 1, 1);
            }
        }
        this._navigationInf.push(current_id);
        this._navigationPoint = this._navigationInf.length - 1;
    }

    private PanelContainsKey(current_id: string): boolean {
        return this._panelModule[current_id] ? true : false;
    }

    public paraParser(para_array: string[]): boolean {
        if (para_array.length < 3) {
            VE_ErrorManager.Add(new VE_Error('', this.ID + "  3" + para_array.length.toString() + "", '', Severity.Error));
            return false;
        } else {

            // 1  GUIstring
            let para1: VeryParas = new VeryParas();
            if (!ParaParserUtility.StringParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
                return false;
            }
            this._controlStr = para1.string;

            // 2  Canvas
            let canvasStr = para_array[1].split(/,|/);
            for (let i = 0, len = canvasStr.length; i < len; i++) {
                canvasStr[i] = canvasStr[i].trim();
                if (!GUIManager.isCreatedCanvas(canvasStr[i])) {
                    VE_ErrorManager.Add(new VE_Error('', this.ID + "  2D " + canvasStr[i] + "2D", '', Severity.Error));
                    return false;
                }
            }

            //   UGUI
            // 
            for (let i = 2, len = para_array.length; i < len; i++) {
                // Excel
                para_array[i] = para_array[i].replace(/\n/g, '');
                let panelID = para_array[i].split(/:|/);
                if (panelID.length !== 2) {
                    VE_ErrorManager.Add(new VE_Error('', this.ID + ' UI ' + para_array[i] + 'idUI1UI2...', '', Severity.Error));
                    return false;
                }

                //   
                var condition1 = /^[0-9a-zA-Z]*$/;
                if (!condition1.test(panelID[0])) {
                    VE_ErrorManager.Add(new VE_Error('', this.ID + ' : ' + para_array[i] + "ID" + panelID[0] + "", '', Severity.Error));
                    return false;
                }

                // Panel id111
                if (!this._panelModule[panelID[0]]) {
                    this._panelModule[panelID[0]] = [];

                    // Debug.Log(panelStr[0]);
                    if (this._length == -1) {
                        this._length = panelID[0].length;
                    }
                    else if (this._length != panelID[0].length) {
                        VE_ErrorManager.Add(new VE_Error('', this.ID + ' : ' + panelID[0] + "", '', Severity.Error));
                        return false;
                    }
                    // 
                    let tagString = panelID[0].substring(0, this._length - 1);
                    let lastNumber = this.ConversionCharToIndex(panelID[0][this._length - 1]);  // ASCIIint
                    if (!this._panelModule[tagString + "0"])
                        this._panelModule[tagString + "0"] = [];

                    // 
                    if (!this._max[tagString])
                        this._max[tagString] = lastNumber;
                    else if (this._max[tagString] < lastNumber)
                        this._max[tagString] = lastNumber;
                    // 
                    if (!this._min[tagString]) {
                        if (lastNumber != 0) {
                            this._zeroNotesID[tagString] = tagString + this.ConversionIndexToStr(lastNumber);
                            this._min[tagString] = lastNumber;
                        }
                        if (!this._currentLevelID[tagString])
                            this._currentLevelID[tagString] = tagString + this.ConversionIndexToStr(lastNumber);
                    }

                    else if (this._min[tagString] > lastNumber) {
                        if (lastNumber != 0) {
                            this._min[tagString] = lastNumber;
                            this._zeroNotesID[tagString] = tagString + this.ConversionIndexToStr(lastNumber);
                        }
                        this._currentLevelID[tagString] = tagString + this.ConversionIndexToStr(lastNumber);
                    }
                } else {
                    // 2
                    console.warn(this.ID + " " + panelID[0] + "ID,");
                    continue;
                }
                let panelStr = panelID[1].split(/,|/);

                // Gameobject
                for (let j = 0; j < panelStr.length; j++) {
                    if (panelStr[j] == "")
                        continue;
                    let panelObj: Nullable<BABYLON.GUI.Control> = null;
                    let isSingleName = (panelStr[j].split(GUIManager.STATE_SEPARATOR).length == 1);
                    for (let k = 0; k < canvasStr.length; k++) {
                        if (isSingleName) {
                            panelObj = GUIManager.GetUGUIGameObejctSingleName(canvasStr[k], panelStr[j]);
                        }
                        else {
                            panelObj = GUIManager.GetUGUIGameObejctFullname(canvasStr[k], panelStr[j]);
                        }
                        if (panelObj !== null) {
                            this._panelModule[panelID[0]].push(panelObj);
                            break;
                        }
                    }
                    if (panelObj === null) {
                        console.error(this.ID + " :" + panelStr[j] + "");
                        return false;
                    }
                }
            }

            // 
            this._currentTotalID = this._controlStr.value;
            this._currentLevelID[this._currentTotalID.substring(0, this._length - 1)] = this._currentTotalID;
            for (let key in this._panelModule) {
                if (this._currentTotalID != key)
                    this.SetActive(this._panelModule[key], false);
            }
            if (this._panelModule[this._currentTotalID]) {
                this.SetActive(this._panelModule[this._currentTotalID], true);
            }

            return true;
        }
    }

    private SetActive(panel_objs: BABYLON.GUI.Control[], is_show: boolean): void {
        for (let i = 0; i < panel_objs.length; i++) {
            panel_objs[i].isVisible = is_show;
        }
    }

    private PlusLoop(current_id: string, plus_number: number = 1): string {
        let lastNumber = this.ConversionCharToIndex(current_id[this._length - 1]);
        let lastNumberStr = "";
        if (lastNumber === 0)
            return current_id;
        current_id = current_id.substring(0, this._length - 1);
        while (true) {
            lastNumber += plus_number;
            if (lastNumber > this._max[current_id])
                lastNumber = this._min[current_id] + lastNumber - this._max[current_id] - 1;
            lastNumberStr = this.ConversionIndexToStr(lastNumber);
            if (this.PanelContainsKey(current_id + lastNumberStr))
                break;
        }
        return current_id + lastNumberStr;
    }

    private MinusLoop(current_id: string, minus_number: number = 1): string {
        let lastNumber = this.ConversionCharToIndex(current_id[this._length - 1]);
        let lastNumberStr = "";
        if (lastNumber === 0)
            return current_id;
        current_id = current_id.substring(0, this._length - 1);
        while (true) {
            lastNumber -= minus_number;
            if (lastNumber < this._min[current_id])
                lastNumber = this._max[current_id] - (this._min[current_id] - lastNumber - 1);
            lastNumberStr = this.ConversionIndexToStr(lastNumber);
            if (this.PanelContainsKey(current_id + lastNumberStr))
                break;
        }
        return current_id + lastNumberStr;
    }

    private ConversionIndexToStr(index: number): string {
        if (index < 10)
            return index.toString();
        else if (index < 36)
            return String.fromCharCode(index + 87);  // 'a' ((char)(index + 87)).ToString();
        else if (index < 62)
            return String.fromCharCode(index + 29);  // ((char)(index + 29)).ToString();
        else {
            console.warn(this.ID + " ");
            return "null";
        }
    }

    private ConversionCharToIndex(char_index: string): number {
        let index = char_index.charCodeAt(0);
        if (96 < index && index < 123) {
            return index - 87;
        } else if (index > 64 && index < 91) {
            return index - 29;
        } else {
            return index - 48;
        }
    }

    public destroy(): void {

    }
}



export class Action_UGUI_Set_Text extends VE_ActionBehaviour {

    public get ID(): string {
        return "GUI_Set_Text|GUI";
    }

    public get className(): string {
        return "Action_UGUI_Set_Text";
    }

    private _control: BABYLON.GUI.Control = new BABYLON.GUI.Control();
    private _TextBlock: BABYLON.GUI.TextBlock = new BABYLON.GUI.TextBlock();
    private _InputText: BABYLON.GUI.InputText = new BABYLON.GUI.InputText();
    private _uiType = "";

    private _blueString: VeryString = new VeryString();
    private _blueNumber: VeryNumber = new VeryNumber();
    private _type: string = "";

    public active(): void {

    }

    public onUpdate(): void {
        if (this._type == "float") {
            if (this._uiType == "TextBlock") {
                this._TextBlock.text = String(this._blueNumber.value);
            }
            else if (this._uiType == "InputText") {
                this._InputText.text = String(this._blueNumber.value);
            }

        }
        else {
            if (this._uiType == "TextBlock") {
                this._TextBlock.text = this._blueString.value;
            }
            else if (this._uiType == "InputText") {
                this._InputText.text = this._blueString.value;
            }
        }
    }

    public paraParser(para_array: string[]): boolean {
        if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 2)) {
            return false;
        } else {
            //
            var tmpControl = UGUI_Tools.GetUI(this.scene, para_array[0], this.ID);
            if (tmpControl == null) {
                console.error(this.ID + "--,UI--" + para_array[0]);
                return false;
            }
            else {
                this._control = tmpControl;
                if (this._control.typeName == "TextBlock") {
                    this._TextBlock = <BABYLON.GUI.TextBlock>this._control;
                    this._uiType = "TextBlock";
                }
                else if (this._control.typeName == "InputText") {
                    this._InputText = <BABYLON.GUI.InputText>this._control;
                    this._uiType = "InputText";
                }
                else if (this._control.typeName == "Button") {
                    var _Button = <BABYLON.GUI.Button>this._control;
                    if (_Button.textBlock != null) {
                        this._TextBlock = _Button.textBlock;
                        this._uiType = "TextBlock";
                    }
                }
                if (this._uiType == "") {
                    console.error(this.ID + "--,UI--" + para_array[0]);
                    return false;
                }
            }

            // 2bool
            let para2: VeryParas = new VeryParas();
            if (!ParaParserUtility.StringParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
                if (!ParaParserUtility.NumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
                    return false;
                }
                else {
                    this._blueNumber = para2.number;
                    this._type = "number";
                }
            }
            else {
                this._blueString = para2.string;
            }

            return true;
        }
    }
    public destroy(): void {

    }
}

