export class Action_UGUI_Set_SizeDelta extends VE_ActionBehaviour {

    public get ID(): string {
        return "GUI_Set_SizeDelta|GUI";
    }

    public get className(): string {
        return "Action_UGUI_Set_SizeDelta";
    }

    private _control: BABYLON.GUI.Control = new BABYLON.GUI.Control();

    private _posValue: VeryNumber = new VeryNumber();
    private _isVec = false;
    private _posVec: VeryVector2 = new VeryVector2();
    private _posVecFloat: VeryNumber[] = [];

    private _axis = 0;

    public active(): void {

    }

    public onUpdate(): void {
        if (this._axis == 0) {
            this._control.width = this._posValue.value + "px";
        }
        else if (this._axis == 1) {
            this._control.height = this._posValue.value + "px";
        }
        else {
            if (!this._isVec) {
                this._control.width = this._posVecFloat[0].value + "px";
                this._control.height = this._posVecFloat[1].value + "px";

            }
            else {
                this._control.width = this._posVec.value.x + "px";
                this._control.height = this._posVec.value.y + "px";
            }
        }
    }

    public paraParser(para_array: string[]): boolean {
        if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 3)) {
            return false;
        } else {
            //
            var tmpControl = UGUI_Tools.GetUI(this.scene, para_array[0], this.ID);
            if (tmpControl == null) {
                console.error(this.ID + "--,UI--" + para_array[0]);
                return false;
            }
            else {
                this._control = tmpControl;
            }

            // 2
            if (para_array[1].toLowerCase() == "x") {
                this._axis = 0;
            }
            else if (para_array[1].toLowerCase() == "y") {
                this._axis = 1;
            }
            else if (para_array[1].toLowerCase() == "vector2") {
                this._axis = 2;
            }
            else {
                console.error(this.ID + " xyzvector2" + para_array[1] + "");
                return false;
            }

            //3
            let para2: VeryParas = new VeryParas();
            if (this._axis == 0 || this._axis == 1) {
                if (!ParaParserUtility.NumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[2])) {
                    return false;
                }
                this._posValue = para2.number;
            }
            else {
                if (!ParaParserUtility.Vector2OrNumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[2])) {
                    return false;
                }
                this._isVec = para2.isVec2;
                this._posVec = para2.vector2;
                this._posVecFloat = para2.vec2Numbers;
            }

            return true;
        }
    }
    public destroy(): void {

    }
}

export class Action_UGUI_Set_AnchoredPosition extends VE_ActionBehaviour {

    public get ID(): string {
        return "GUI_Set_AnchoredPosition|GUI";
    }

    public get className(): string {
        return "Action_UGUI_Set_AnchoredPosition";
    }

    private _control: BABYLON.GUI.Control = new BABYLON.GUI.Control();

    private _posValue: VeryNumber = new VeryNumber();
    private _isVec = false;
    private _posVec: VeryVector2 = new VeryVector2();
    private _posVecFloat: VeryNumber[] = [];

    private _axis = 0;

    public active(): void {

    }

    public onUpdate(): void {
        if (this._axis == 0) {
            this._control.left = this._posValue.value + "px";
        }
        else if (this._axis == 1) {
            this._control.top = this._posValue.value + "px";
        }
        else {
            if (!this._isVec) {
                this._control.left = this._posVecFloat[0].value + "px";
                this._control.top = this._posVecFloat[1].value + "px";

            }
            else {
                this._control.left = this._posVec.value.x + "px";
                this._control.top = this._posVec.value.y + "px";
            }
        }
    }

    public paraParser(para_array: string[]): boolean {
        if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 3)) {
            return false;
        } else {
            //
            var tmpControl = UGUI_Tools.GetUI(this.scene, para_array[0], this.ID);
            if (tmpControl == null) {
                console.error(this.ID + "--,UI--" + para_array[0]);
                return false;
            }
            else {
                this._control = tmpControl;
            }

            // 2
            if (para_array[1].toLowerCase() == "x") {
                this._axis = 0;
            }
            else if (para_array[1].toLowerCase() == "y") {
                this._axis = 1;
            }
            else if (para_array[1].toLowerCase() == "vector2") {
                this._axis = 2;
            }
            else {
                console.error(this.ID + " xyzvector2" + para_array[1] + "");
                return false;
            }

            //3
            let para2: VeryParas = new VeryParas();
            if (this._axis == 0 || this._axis == 1) {
                if (!ParaParserUtility.NumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[2])) {
                    return false;
                }
                this._posValue = para2.number;
            }
            else {
                if (!ParaParserUtility.Vector2OrNumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[2])) {
                    return false;
                }
                this._isVec = para2.isVec2;
                this._posVec = para2.vector2;
                this._posVecFloat = para2.vec2Numbers;
            }

            return true;
        }
    }
    public destroy(): void {

    }
}

export class Action_UGUI_Get_AnchoredPosition extends VE_ActionBehaviour {

    public get ID(): string {
        return "GUI_Get_AnchoredPosition|GUI";
    }

    public get className(): string {
        return "Action_UGUI_Get_AnchoredPosition";
    }

    private _control: BABYLON.GUI.Control = new BABYLON.GUI.Control();

    private _posValue: VeryNumber = new VeryNumber();
    private _posVec: VeryVector2 = new VeryVector2();

    private _axis = 0;

    public active(): void {

    }

    public onUpdate(): void {
        if (this._axis == 0) {
            this._posValue.value = this._control.leftInPixels;
        }
        else if (this._axis == 1) {
            this._posValue.value = this._control.topInPixels;
        }
        else {
            this._posVec.value = new BABYLON.Vector2(this._control.leftInPixels, this._control.topInPixels);
        }
    }

    public paraParser(para_array: string[]): boolean {
        if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 3)) {
            return false;
        } else {
            //
            var tmpControl = UGUI_Tools.GetUI(this.scene, para_array[0], this.ID);
            if (tmpControl == null) {
                console.error(this.ID + "--,UI--" + para_array[0]);
                return false;
            }
            else {
                this._control = tmpControl;
            }

            // 2
            if (para_array[1].toLowerCase() == "x") {
                this._axis = 0;
            }
            else if (para_array[1].toLowerCase() == "y") {
                this._axis = 1;
            }
            else if (para_array[1].toLowerCase() == "vector2") {
                this._axis = 2;
            }
            else {
                console.error(this.ID + " xyzvector2" + para_array[1] + "");
                return false;
            }

            //3
            let para2: VeryParas = new VeryParas();
            if (this._axis == 0 || this._axis == 1) {
                if (!ParaParserUtility.NumberParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[2])) {
                    return false;
                }
                this._posValue = para2.number;
            }
            else {
                if (!ParaParserUtility.Vector2Para(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[2])) {
                    return false;
                }
                this._posVec = para2.vector2;
            }

            return true;
        }
    }
    public destroy(): void {

    }
}

export class Action_UGUI_Set_Color extends VE_ActionBehaviour {

    public get ID(): string {
        return "GUI_Set_Color|GUI";
    }

    public get className(): string {
        return "Action_UGUI_Set_Color";
    }

    private _control: BABYLON.GUI.Control = new BABYLON.GUI.Control();
    private _isColor: boolean = false;
    private _blueColor: VeryVector3 = new VeryVector3();
    private _colorFloat: VeryNumber[] = [];

    public active(): void {

    }

    public onUpdate(): void {
        if (!this._isColor) {
            this._control.color = this.RGBToHexColor(this._colorFloat[0].value / 255, this._colorFloat[1].value / 255, this._colorFloat[2].value / 255);
        }
        else {
            this._control.color = this.RGBToHexColor(this._blueColor.value.x / 255, this._blueColor.value.y / 255, this._blueColor.value.z / 255);
        }
    }

    public paraParser(para_array: string[]): boolean {
        if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 2)) {
            return false;
        } else {
            //
            var tmpControl = UGUI_Tools.GetUI(this.scene, para_array[0], this.ID);
            if (tmpControl == null) {
                console.error(this.ID + "--,UI--" + para_array[0]);
                return false;
            }
            else {
                this._control = tmpControl;
            }

            // 2
            let para3: VeryParas = new VeryParas();
            if (!ParaParserUtility.Vector3OrNumberParaWithExpression(this.projectName, this.objectID, this.ID, ParaType.Trigger, para3, para_array[1])) {
                return false;
            }
            this._isColor = para3.isVec;
            this._blueColor = para3.vector3;
            this._colorFloat = para3.vecNumbers;

            return true;
        }
    }

    //RGB#FFFFFF
    public RGBToHexColor(r: number, g: number, b: number): string {
        var str = "#";
        var tmpStr = new Number((r * 255).toFixed()).toString(16);
        if (tmpStr.length == 1) {
            tmpStr = "0" + tmpStr;
        }
        str += tmpStr;
        var tmpStr = new Number((g * 255).toFixed()).toString(16);
        if (tmpStr.length == 1) {
            tmpStr = "0" + tmpStr;
        }
        str += tmpStr;
        var tmpStr = new Number((b * 255).toFixed()).toString(16);
        if (tmpStr.length == 1) {
            tmpStr = "0" + tmpStr;
        }
        str += tmpStr;
        return str;
    }
    public destroy(): void {

    }
}

