import { VE_TriggerBehaviour, VE_Triggers } from "../../trigger";
import { VeryGameObject, VeryBool, VeryNumber, VeryString } from "../../variables";
import { ParaParserUtility, VeryParas } from "../../utility";
import { ParaType, Severity } from "../../enum";
import { VE_ErrorManager, VE_Error } from "../../global";
import { Action_Debug } from "../action";

export class Trigger_UGUI_Button extends VE_TriggerBehaviour {

    public get ID(): string {
        return 'GUI_Button| ';
    };

    public get className(): string {
        return 'Trigger_UGUI_Button';
    };

    private _button: BABYLON.GUI.Button = new BABYLON.GUI.Button();

    private _observer: Nullable<BABYLON.Observer<BABYLON.GUI.Vector2WithInfo>> = null;

    public paraParser(para_array: string[]): boolean {

        if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Trigger, para_array.length, 1)) {
            return false;
        }
        else {
            //
            var tmpControl = UGUI_Tools.GetUI(this.scene, para_array[0], this.ID);
            if (tmpControl == null) {
                console.error(this.ID + "----" + para_array[0]);
                return false;
            }
            else {
                this._button = <BABYLON.GUI.Button>tmpControl;
            }

            this._observer = this._button.onPointerDownObservable.add(() => {
                this.sendEvent();
            });

            return true;
        }
    }
    public destroy(): void {
        if (this._observer) {
            this._button.onPointerDownObservable.remove(this._observer);
        }
        this._observer = null;
    }
}

export class Trigger_UGUI_Event_Trigger extends VE_TriggerBehaviour {

    public get ID(): string {
        return 'GUI_Event_Trigger|GUI ';
    };

    public get className(): string {
        return 'Trigger_UGUI_Event_Trigger';
    };

    private _control: BABYLON.GUI.Control = new BABYLON.GUI.Control();
    private _eventType: string = "";

    private _moveObserver: Nullable<BABYLON.Observer<BABYLON.Vector2>> = null;
    private _outObserver: Nullable<BABYLON.Observer<BABYLON.GUI.Control>> = null;
    private _downObserver: Nullable<BABYLON.Observer<BABYLON.GUI.Vector2WithInfo>> = null;
    private _upObserver: Nullable<BABYLON.Observer<BABYLON.GUI.Vector2WithInfo>> = null;
    private _clickObserver: Nullable<BABYLON.Observer<BABYLON.GUI.Vector2WithInfo>> = null;
    private _enterObserver: Nullable<BABYLON.Observer<BABYLON.GUI.Control>> = null;


    public paraParser(para_array: string[]): boolean {

        if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Trigger, para_array.length, 2)) {
            return false;
        }
        else {
            //
            var tmpControl = UGUI_Tools.GetUI(this.scene, para_array[0], this.ID);
            if (tmpControl == null) {
                console.error(this.ID + "----" + para_array[0]);
                return false;
            }
            else {
                this._control = tmpControl;
            }

            //2
            this._eventType = para_array[1];
            if (this._eventType == "") {
                this._moveObserver = this._control.onPointerMoveObservable.add(() => {
                    this.sendEvent();
                });
            }
            else if (this._eventType == "") {
                this._outObserver = this._control.onPointerOutObservable.add(() => {
                    this.sendEvent();
                });
            }
            else if (this._eventType == "") {
                this._downObserver = this._control.onPointerDownObservable.add(() => {
                    this.sendEvent();
                });
            }
            else if (this._eventType == "") {
                this._upObserver = this._control.onPointerUpObservable.add(() => {
                    this.sendEvent();
                });
            }
            else if (this._eventType == "") {
                this._clickObserver = this._control.onPointerClickObservable.add(() => {
                    this.sendEvent();
                });
            }
            else if (this._eventType == "") {
                this._enterObserver = this._control.onPointerEnterObservable.add(() => {
                    this.sendEvent();
                });
            }
            else {
                console.error(this.ID + "----" + this._eventType);
                return false;
            }

            return true;
        }
    }
    public destroy(): void {
        if (this._moveObserver) {
            this._control.onPointerMoveObservable.remove(this._moveObserver);
        }
        if (this._outObserver) {
            this._control.onPointerOutObservable.remove(this._outObserver);
        }
        if (this._downObserver) {
            this._control.onPointerDownObservable.remove(this._downObserver);
        }
        if (this._upObserver) {
            this._control.onPointerUpObservable.remove(this._upObserver);
        }
        if (this._clickObserver) {
            this._control.onPointerClickObservable.remove(this._clickObserver);
        }
        if (this._enterObserver) {
            this._control.onPointerEnterObservable.remove(this._enterObserver);
        }
        this._moveObserver = null;
        this._outObserver = null;
        this._downObserver = null;
        this._upObserver = null;
        this._clickObserver = null;
        this._enterObserver = null;
    }
}

export class UGUI_Tools {
    /*
      UI
    */
    public static GetUI(scene: BABYLON.Scene, buttonName: string, id: string): BABYLON.Nullable<BABYLON.GUI.Control> {
        var control: BABYLON.Nullable<BABYLON.GUI.Control> = null;
        var array: string[] = buttonName.split(".");
        var tmpTexture: BABYLON.GUI.AdvancedDynamicTexture;
        var hasAdvancedDynamicTexture = false;
        var hasUI = false;
        for (var i = 0; i < scene.textures.length; i++) {
            if (array[0] == scene.textures[i].name) {
                tmpTexture = <BABYLON.GUI.AdvancedDynamicTexture>scene.textures[i];
                var tmpContainer = tmpTexture.rootContainer;
                for (var i = 1; i < array.length; i++) {
                    var tmpControl = tmpContainer.getChildByName(array[i]);
                    if (tmpControl == null) {
                        hasUI = false;
                        break;
                    }
                    else {
                        if (i != (array.length - 1)) {
                            tmpContainer = <BABYLON.GUI.Container>tmpControl;
                        }
                        else {
                            return tmpControl;
                        }
                    }
                }
                hasAdvancedDynamicTexture = true;
                break;
            }
        }
        if (!hasAdvancedDynamicTexture || !hasUI) {
            console.error(id + "----" + buttonName);
        }

        return control;
    }
}

//GUI
VE_Triggers.addTrigger(Trigger_UGUI_Button);
VE_Triggers.addTrigger(Trigger_UGUI_Event_Trigger);