import { VE_ActionBehaviour, VE_Actions } from "../../action";
import { ParaParserUtility, VeryParas } from "../../utility";
import { ParaType } from "../../enum";
import { VeryNumber, VeryString, VeryVector3 } from "../../variables";
import { BabylonEngine } from "../../babylon";

export class Action_Skybox extends VE_ActionBehaviour {

  public get ID(): string {
    return "Skybox|";
  }

  public get className(): string {
    return "Action_Skybox";
  }

  private _blueString: VeryString = new VeryString();

  public active(): void {
    for (var i = 0; i < BabylonEngine.Scene.meshes.length; i++) {
      BabylonEngine.Scene.meshes[i].renderingGroupId = 1;
    }
    var skybox = BABYLON.MeshBuilder.CreateBox("skyBox", {
      size: 1000.0
    }, BabylonEngine.Scene);
    skybox.infiniteDistance = true;
    skybox.renderingGroupId = 0;
    var skyboxMaterial = new BABYLON.StandardMaterial("skyBox", BabylonEngine.Scene);
    skyboxMaterial.backFaceCulling = false;
    skyboxMaterial.reflectionTexture = new BABYLON.CubeTexture(
      this._blueString.value, BabylonEngine.Scene);
    skyboxMaterial.reflectionTexture.coordinatesMode = BABYLON.Texture.SKYBOX_MODE;
    skyboxMaterial.diffuseColor = new BABYLON.Color3(0, 0, 0);
    skyboxMaterial.specularColor = new BABYLON.Color3(0, 0, 0);
    skybox.material = skyboxMaterial;
  }

  public onUpdate(): void {
  }

  public paraParser(para_array: string[]): boolean {
    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 1)) {
      return false;
    } else {
      // 1
      let para1: VeryParas = new VeryParas();
      if (!ParaParserUtility.StringPara(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
        return false;
      }
      this._blueString = para1.string;

      return true;
    }
  }

  public destroy(): void {

  }

}

VE_Actions.addAction(Action_Skybox);
