import { ParaParserUtility, Tools, VeryParas } from "../../../veryengine/utility";
import { VE_ActionBehaviour, VE_Actions } from "../../../veryengine/action";
import { ParaType, Severity } from "../../../veryengine/enum";
import { VE_Error, VE_ErrorManager } from "../../../veryengine/global";
import { BabylonEngine } from "../../../veryengine/babylon";

export class Action_Focus_Camera extends VE_ActionBehaviour {

    public get ID(): string {
        return "Focus_Camera|";
    }

    public get className(): string {
        return "Action_Focus_Camera";
    }


    private orbiting = false;
    private panning = false;

    private target: Nullable<BABYLON.TransformNode>;
    private cameraObj: Nullable<BABYLON.TransformNode>;
    private camera: Nullable<BABYLON.Camera>;

    private MouseWheelSensitivity = 2; //
    private MouseZoomMin = 0.1; //
    private MouseZoomMax = 10000; //
    private moveSpeedReal = 1; //
    private moveSpeed = 1; 
    private wheelSpeed = 0.01;

    private xSpeed = 250.0; //x
    private ySpeed = 120.0; //y

    private yMinLimit = -360;
    private yMaxLimit = 360;

    private x = 0.0; //euler
    private y = 0.0; //euler

    private StandardDistance = 300;
    private Distance = 300; //targetZtargetz
    private targetOnScreenPosition: BABYLON.Vector3; //z
    private storeRotation: BABYLON.Quaternion; //
    private position: BABYLON.Vector3;
    private CameraTargetPosition: BABYLON.Vector3; //target
    private initPosition: BABYLON.Vector3 = BABYLON.Vector3.Zero(); //
    private cameraX: BABYLON.Vector3; //x
    private cameraY: BABYLON.Vector3; //y
    // private cameraZ: BABYLON.Vector3; //z

    private initScreenPos: BABYLON.Vector3; //
    private curScreenPos: BABYLON.Vector3; //


    private focusTarget = BABYLON.Vector3.Zero();
    private focusPoint = BABYLON.Vector3.Zero();
    private focusOrthoHeight = 0;
    // var focusCamera;
    private focusing = false;
    private firstUpdate = false;
    private flySpeed = 0.25;
    private vecA = BABYLON.Vector3.Zero();
    private vecB = BABYLON.Vector3.Zero();


    private _initialized: boolean = false;
    private _observer: Nullable<BABYLON.Observer<BABYLON.PointerInfo>> = null;
    private canvas: HTMLCanvasElement;

    public constructor() {
        super();
        this.canvas = BabylonEngine.Canvas;
    }

    public active(): void {
        // 
        if (!this._initialized && this.cameraObj && this.target) {
            this._initialized = true;

            var angles = Tools.radianToEulerAngle(this.cameraObj.rotation);
            this.x = angles.y;
            this.y = angles.x;

            this.CameraTargetPosition = this.target.position.clone();
            this.targetOnScreenPosition = this.target.position.clone();

            this.Distance = this.cameraObj.position.subtract(this.target.position).length();

            this.storeRotation = BABYLON.Quaternion.FromEulerVector(Tools.eulerAngleToRadian(new BABYLON.Vector3(this.y + 60, this.x, 0)));
            // this.cameraObj.rotation = this.storeRotation.toEulerAngles(); //
            this.position = Tools.quatMultiplyVector3(this.storeRotation, new BABYLON.Vector3(0.0, 0.0, -this.Distance)).add(this.CameraTargetPosition); //
            // this.cameraObj.position.copyFrom(this.position); //
        }
    }

    public onUpdate(): void {
    }

    public paraParser(para_array: string[]): boolean {

        if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Action, para_array.length, 5)) {
            return false;
        } else {
            // 1VeryCamera
            let para1: VeryParas = new VeryParas();
            if (!ParaParserUtility.GameObjectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para1, para_array[0])) {
                return false;
            }
            this.cameraObj = para1.gameObject.value.transform.transformNode;
            if (this.cameraObj instanceof BABYLON.TransformNode && (<any>this.cameraObj).camera instanceof BABYLON.Camera) {
                this.camera = (<any>this.cameraObj).camera;
                if (this.camera.mode === BABYLON.Camera.ORTHOGRAPHIC_CAMERA) {
                    this.camera.minZ = -1000;
                }
            } else {
                VE_ErrorManager.Add(new VE_Error('', `${this.ID} ${para_array[0]}`, '', Severity.Error));
                return false;
            }

            // console.warn(this.cameraObj);
            // console.warn((<any>this.cameraObj).camera);
            // console.warn((<any>this.cameraObj).fov);
            // console.warn((<any>this.cameraObj).orthoSize);

            // 2: 
            let para2: VeryParas = new VeryParas();
            if (!ParaParserUtility.GameObjectPara(this.projectName, this.objectID, this.ID, ParaType.Action, para2, para_array[1])) {
                return false;
            }
            this.target = para2.gameObject.value.transform.transformNode;

            // 3: 
            let para3 = new VeryParas();
            if (!ParaParserUtility.NumberPara(this.projectName, this.objectID, this.ID, ParaType.Action, para3, para_array[2])) {
                return false;
            }
            this.xSpeed = para3.number.value;
            this.ySpeed = this.xSpeed * 0.5;

            // 4: 
            let para4 = new VeryParas();
            if (!ParaParserUtility.NumberPara(this.projectName, this.objectID, this.ID, ParaType.Action, para4, para_array[3])) {
                return false;
            }
            this.moveSpeed = para4.number.value;
            
            // 5: 
            let para5 = new VeryParas();
            if (!ParaParserUtility.NumberPara(this.projectName, this.objectID, this.ID, ParaType.Action, para5, para_array[4])) {
                return false;
            }
            this.MouseWheelSensitivity = para5.number.value;

            let self = this;
            this._observer = this.scene.onPointerObservable.add(pointInfo => {
                if (!self.enabled) {
                    return;
                }
                if (pointInfo.type === BABYLON.PointerEventTypes.POINTERDOWN) {
                    if (!self.focusing) {
                        // 
                        if (pointInfo.event.button === 1) {
                            self.panning = true;

                            self.cameraX = self.cameraObj.right;
                            self.cameraY = self.cameraObj.up;
                            // self.cameraZ = self.cameraObj.forward;
                            let rect = this.canvas.getBoundingClientRect();
                            self.initScreenPos = new BABYLON.Vector3(pointInfo.event.clientX - rect.left, rect.height - pointInfo.event.clientY, self.targetOnScreenPosition.z);

                            // targetOnScreenPosition.zxmidbuttonDownPositiony
                            self.targetOnScreenPosition = Tools.worldToScreenPoint(self.CameraTargetPosition, self.scene, <BABYLON.TargetCamera>self.camera, BabylonEngine.Engine);

                            self.initPosition.copyFrom(self.CameraTargetPosition);
                        }

                        // 
                        if (pointInfo.event.button === 2) {
                            self.orbiting = true;
                        }
                    }
                } else if (pointInfo.type === BABYLON.PointerEventTypes.POINTERUP) {
                    // 
                    if (pointInfo.event.button === 1) {
                        self.panning = false;
                        //cameraTargetPosition
                        self.CameraTargetPosition.copyFrom(self.target.position);
                    }

                    // 
                    if (pointInfo.event.button === 2) {
                        self.orbiting = false;
                    }
                } else if (pointInfo.type === BABYLON.PointerEventTypes.POINTERMOVE) {
                    if (!self.focusing) {
                        if (self.orbiting) {
                            self.x += pointInfo.event.movementX * self.xSpeed * 0.002;
                            self.y -= -pointInfo.event.movementY * self.ySpeed * 0.002;
                            self.y = Tools.clampAngle(self.y, self.yMinLimit, self.yMaxLimit);
                            self.storeRotation = BABYLON.Quaternion.FromEulerVector(Tools.eulerAngleToRadian(new BABYLON.Vector3(self.y + 60, self.x, 0)));
                            self.position = Tools.quatMultiplyVector3(self.storeRotation, new BABYLON.Vector3(0.0, 0.0, -self.Distance)).add(self.CameraTargetPosition);

                            self.cameraObj.rotation = self.storeRotation.toEulerAngles(); //
                            self.cameraObj.position.copyFrom(self.position);
                        }

                        if (self.panning) {
                            // console.warn(evt);
                            // console.error(rect);
                            let rect = this.canvas.getBoundingClientRect();
                            self.curScreenPos = new BABYLON.Vector3(pointInfo.event.clientX - rect.left, rect.height - pointInfo.event.clientY, self.targetOnScreenPosition.z);
                            //0.01distance
                            if (self.camera.mode === BABYLON.Camera.PERSPECTIVE_CAMERA) {
                                self.moveSpeedReal = 0.1 * self.moveSpeed;
                            } else {
                                if ((<any>self.cameraObj).orthoSize < 0.05) {
                                    self.moveSpeedReal = 0.04 * self.moveSpeed;
                                } else {
                                    self.moveSpeedReal = 0.1 * self.moveSpeed;
                                }
                            }
                            self.cameraObj.rotation = self.storeRotation.toEulerAngles(); //
                            self.target.position = self.initPosition.subtract(self.cameraX.scale(self.curScreenPos.x - self.initScreenPos.x).add(self.cameraY.scale(self.curScreenPos.y - self.initScreenPos.y)).scale(self.moveSpeedReal));

                            //
                            let mPosition = Tools.quatMultiplyVector3(self.storeRotation, new BABYLON.Vector3(0.0, 0.0, -self.Distance)).add(self.target.position);
                            self.cameraObj.position.copyFrom(mPosition);
                        }
                    }
                } else if (pointInfo.type === BABYLON.PointerEventTypes.POINTERWHEEL) {
                    if (!self.focusing) {
                        self.cameraObj.rotation = self.storeRotation.toEulerAngles(); //
                        if (self.camera.mode === BABYLON.Camera.PERSPECTIVE_CAMERA) {
                            if (self.Distance >= self.MouseZoomMin && self.Distance <= self.MouseZoomMax) {
                                self.Distance += (<WheelEvent>pointInfo.event).deltaY * self.MouseWheelSensitivity * 0.05;
                            }
                            if (self.Distance < self.MouseZoomMin) {
                                self.Distance = self.MouseZoomMin;
                            }
                            if (self.Distance > self.MouseZoomMax) {
                                self.Distance = self.MouseZoomMax;
                            }

                            // console.log('distance: ' + Distance);
                            // console.log('delta: ' + (evt.deltaY * MouseWheelSensitivity * 0.05));
                            self.position = Tools.quatMultiplyVector3(self.storeRotation, new BABYLON.Vector3(0.0, 0.0, -self.Distance)).add(self.CameraTargetPosition);
                            self.cameraObj.position.copyFrom(self.position);
                        } else {
                            self.wheelSpeed = 0.5;
                            if (self.Distance >= self.MouseZoomMin && self.Distance <= self.MouseZoomMax) {
                                self.Distance += (<WheelEvent>pointInfo.event).deltaY * self.MouseWheelSensitivity * 0.05 * self.wheelSpeed;
                            }
                            if (self.Distance < self.MouseZoomMin) {
                                self.Distance = self.MouseZoomMin;
                            }
                            if (self.Distance > self.MouseZoomMax) {
                                self.Distance = self.MouseZoomMax;
                            }

                            // console.log('distance: ' + Distance);
                            // console.log('delta: ' + (evt.deltaY * MouseWheelSensitivity * 0.05));

                            let delta = (<WheelEvent>pointInfo.event).deltaY * self.MouseWheelSensitivity * 0.05 * 0.0012;

                            if ((<any>self.cameraObj).orthoSize > 0.01 || ((<any>self.cameraObj).orthoSize <= 0.01 && delta > 0)) {
                                (<any>self.cameraObj).orthoSize += delta;
                                self.position = Tools.quatMultiplyVector3(self.storeRotation, new BABYLON.Vector3(0.0, 0.0, -self.Distance)).add(self.CameraTargetPosition);
                                self.cameraObj.position.copyFrom(self.position);
                            }
                            if ((<any>self.cameraObj).orthoSize <= 0.01) {
                                (<any>self.cameraObj).orthoSize = 0.01;
                            }

                            // console.warn(VeryEngine.viewCamera.orthoSize);
                        }
                    }
                }
            });

        }
        return true;
    }

    public destroy(): void {
        if (this._observer) {
            this.scene.onPointerObservable.remove(this._observer);
        }
        this._observer = null;
    }

}

VE_Actions.addAction(Action_Focus_Camera);