export class GUIManager {

    public static UGUI_SEPARATOR = '_';
    public static STATE_SEPARATOR = '.';
    private static _guiDic: { [key: string]: GUIComponent } = {};


    public static createCanvas(canvas: BABYLON.GUI.AdvancedDynamicTexture): void {
        GUIManager._guiDic[canvas.name] = new GUIComponent(canvas.name, canvas);
        GUIManager._guiDic[canvas.name].add(canvas.name, canvas.name, canvas.rootContainer);
    }

    public static addControl(control: BABYLON.GUI.Control): void {
        // fullname
        let fullname = GUIManager.getFullname(control.name, control.parent);
        if(GUIManager._guiDic[control.host.name]) {
            GUIManager._guiDic[control.host.name].add(control.name, fullname, control);
        }
    }

    public static getFullname(prefix: string, control: Nullable<BABYLON.GUI.Control>): string {
        if (!control) {
            return prefix;
        }
        if (control.parent === control.host.rootContainer) {
            prefix = control.host.name + GUIManager.UGUI_SEPARATOR + prefix;
        } else {
            prefix = control.name + GUIManager.UGUI_SEPARATOR + prefix;
            return GUIManager.getFullname(prefix, control.parent);
        }
        return prefix;
    }

    public static isCreatedCanvas(canvas_name: string): boolean {
        return GUIManager._guiDic[canvas_name] ? true : false;
    }

    public static GetUGUIGameObejctSingleName(canvas_name: string, single_name: string): Nullable<BABYLON.GUI.Control> {
        if (!GUIManager._guiDic[canvas_name]) {
            return null;
        }
        return GUIManager._guiDic[canvas_name].getSingleNameGUI(single_name);
    }

    public static GetUGUIGameObejctFullname(canvas_name: string, full_name: string): Nullable<BABYLON.GUI.Control> {
        full_name = full_name.replace(/./g, GUIManager.UGUI_SEPARATOR);
        if (!GUIManager._guiDic[canvas_name]) {
            return null;
        }
        return GUIManager._guiDic[canvas_name].getFullnameGUI(canvas_name + GUIManager.UGUI_SEPARATOR + full_name);
    }

}


export class GUIComponent {


    private _canvasName: string;
    public get canvasName(): string {
        return this._canvasName;
    }
    public set canvasName(val: string) {
        this._canvasName = val;
    }

    public canvas: BABYLON.GUI.AdvancedDynamicTexture;

    private _canvasFullname: { [key: string]: BABYLON.GUI.Control } = {};
    private _canvasSingleName: { [key: string]: BABYLON.GUI.Control } = {};


    public constructor(canvas_name: string, canvas: BABYLON.GUI.AdvancedDynamicTexture) {
        this._canvasName = canvas_name;
        this.canvas = canvas;
    }

    public add(single_name: string, fullname: string, control: BABYLON.GUI.Control) {
        this._canvasSingleName[single_name] = control;
        this._canvasFullname[fullname] = control;
    }

    public isCreated(canvas_name: string, control_name: string): boolean {
        let fullname: string;
        if (control_name.startsWith(canvas_name + GUIManager.UGUI_SEPARATOR)) {
            fullname = control_name;
        } else {
            fullname = canvas_name + GUIManager.UGUI_SEPARATOR + control_name;
        }
        if (this._canvasFullname[fullname]) {
            return true;
        } else {
            return false;
        }
    }

    public getSingleNameGUI(single_name: string): Nullable<BABYLON.GUI.Control> {
        return this._canvasSingleName[single_name] ? this._canvasSingleName[single_name] : null;
    }

    public getFullnameGUI(full_name: string): Nullable<BABYLON.GUI.Control> {
        return this._canvasFullname[full_name] ? this._canvasFullname[full_name] : null;
    }


}