export class Trigger_Collision_Enter extends VE_TriggerBehaviour {

  public get ID(): string {
    return 'Collision_Enter|';
  };

  public get className(): string {
    return 'Trigger_Collision_Enter';
  };

  private _storeGameObject: VeryGameObject = new VeryGameObject();
  private _colliderGameObjectList: VeryString[] = [];
  private _target: VeryGameObject = new VeryGameObject();

  private _colliderMeshList: BABYLON.AbstractMesh[] = [];

  public paraParser(para_array: string[]): boolean {

    if (!ParaParserUtility.ParaNumbers(this.ID, ParaType.Trigger, para_array.length, 1, 2, 3)) {
      return false;
    }
    else {
      //1
      if (para_array[0].toLowerCase() == "null" || para_array[0].toLowerCase() == "none") {

      }
      else {
        let para1: VeryParas = new VeryParas();
        if (!ParaParserUtility.GameObjectParaOnlyVar(this.projectName, this.objectID, this.ID, ParaType.Trigger, para1, para_array[0])) {
          return false;
        }
        this._storeGameObject = para1.gameObject;
      }

      //2
      if (para_array.length > 1) {
        if (para_array[1].toLowerCase() == "null" || para_array[1].toLowerCase() == "none") {

        }
        else {
          var strs = para_array[1].split(/,|/);
          var isRight = true;
          for (var i = 0; i < strs.length; i++) {
            strs[i] = strs[i].trim();
            let para1: VeryParas = new VeryParas();
            if (!ParaParserUtility.StringPara(this.projectName, this.objectID, this.ID, ParaType.Trigger, para1, strs[i])) {
              console.error(this.ID + " " + strs[i] + "string");
              isRight = false;
              continue;
            }
            this._colliderGameObjectList[this._colliderGameObjectList.length] = para1.string;
          }
          if (!isRight) {
            return false;
          }
        }
      }

      // 3 
      if (para_array.length > 2) {
        let para1: VeryParas = new VeryParas();
        if (!ParaParserUtility.GameObjectPara(this.projectName, this.objectID, this.ID, ParaType.Trigger, para1, para_array[2])) {
          return false;
        }
        this._target = para1.gameObject;
      } else {
        // 
        this._target.value = this.gameObject;
      }

      return true;
    }
  }

  public onUpdate(): void {
    var tmpTarget = this._target.value?.mesh;
    if (this.enabled && (tmpTarget != null && tmpTarget != undefined)) {
      if (this._colliderGameObjectList.length == 0) {
        for (var i = 0; i < BabylonEngine.Scene.meshes.length; i++) {
          var tmpMesh = BabylonEngine.Scene.meshes[i];
          if (tmpTarget != tmpMesh) {
            if (tmpTarget.intersectsMesh(tmpMesh, true)) {
              if (this._colliderMeshList.indexOf(tmpMesh) == -1) {
                this._storeGameObject.value = GameObject.Find(tmpMesh.name, BabylonEngine.Scene);
                this.sendEvent();
                this._colliderMeshList[this._colliderMeshList.length] = tmpMesh;
                break;
              }
            }
            else {
              if (this._colliderMeshList.indexOf(tmpMesh) != -1) {
                this._colliderMeshList.splice(this._colliderMeshList.indexOf(tmpMesh), 1);
              }
            }
          }
        }
      }
      else {
        for (var i = 0; i < this._colliderGameObjectList.length; i++) {
          var tmpMesh2 = BabylonEngine.Scene.getMeshByName(this._colliderGameObjectList[i].value);
          if (tmpMesh2 != null) {
            if (tmpTarget.intersectsMesh(tmpMesh2, true)) {
              if (this._colliderMeshList.indexOf(tmpMesh2) == -1) {
                this._storeGameObject.value = GameObject.Find(tmpMesh2.name, BabylonEngine.Scene);
                this.sendEvent();
                this._colliderMeshList[this._colliderMeshList.length] = tmpMesh2;
                break;
              }
            }
            else {
              if (this._colliderMeshList.indexOf(tmpMesh2) != -1) {
                this._colliderMeshList.splice(this._colliderMeshList.indexOf(tmpMesh2), 1);
              }
            }
          }
        }
      }
    }
  }

  public destroy(): void {

  }
}

